body {
  margin: 100px;
  padding: 0 0 150px 0;
  font-family: Arial Narrow;
}

.tagline {
  color: gray;
  font-size: 16px;
}

.name {
  font-size: 24px;
}

.login {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  color: black;
  z-index: 1050 !important;
  display: flex;
  flex-direction: column;
  /* justify-content: flex-start; */
  align-items: center;
  align-content: center;
}

.login-menu {
  max-width: 400px;
  border: black 1px solid;
  border-radius: 15px;
  /* width: 50%; */
  padding: 50px;
  margin: 50px;
  box-shadow: 5px 5px 2px 2px #888888;
}

.logo {
  max-width: 300px;
  margin: 20px 0;
}

.profile-pic {
  max-width: 100px;
  margin: 20px;
  border-radius: 50%;
}

.login button {
  padding: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 1px #888888;
}

.hide {
  display: none;
}
.event-card {
  border: 2px solid black;
  margin: 10px;
  padding: 0 !important;
  border-radius: 10px;
  box-shadow: black;
}
.event-card a{
  text-decoration: none;
}

.event-card-heading {
  height: 100px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 0 !important;
  border-radius: 8px;
}

.event-card-info {
  padding: 10px;
}

.event-card-info svg, #text {
  margin: 5px;
}

.nav-menu {
  width: 100%;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  /* align-content: center; */
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: white;
  height: 90px;
  text-align: center;
  border: 1px solid black;
}

.menu-icon {
  font-size: 40px;
}

.map {
  width: 100% !important;
  height: 100% !important;
  margin: 60px 0 0 0;
}

