.filter-menu {
  border: 1px black solid;
  background-color: white;
  position: fixed;
  z-index: 2;
  width: 100%;
  height: 60px;
  justify-content: space-evenly;
  align-items: center;
  display: flex; /* [1] */
  flex-wrap: nowrap; /* [1] */
  overflow-x: auto; /* [2] */
  -webkit-overflow-scrolling: touch; /* [3] */
  -ms-overflow-style: -ms-autohiding-scrollbar; /* [4] */
}

.filter-label-selector input {
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.filter-label {
  cursor: pointer;
  align-content: center;
  width: 100px;
  border: 1px black solid;
  border-radius: 5px;
  text-align: center;
  margin: 10px;
  padding: 5px;
}
.filter-label:hover {
  -webkit-filter: brightness(1.2) grayscale(0.5) opacity(0.9);
  -moz-filter: brightness(1.2) grayscale(0.5) opacity(0.9);
  filter: brightness(1.2) grayscale(0.5) opacity(0.9);
}
.filter-menu-icon {
  font-size: 32px;
  border: 0 !important;
}

.active {
  background-color: rgba(35, 165, 35, 0.932) !important;
  color: white;
}
